/* Doodle CSS - Fresh start */

/* Animatable hue property for color cycling */
@property --hue {
	syntax: "<number>";
	inherits: true;
	initial-value: 210;
}

/* Center the slide */
section[intro] {
	display: grid;
	place-items: center;
}

/* Stage (16:9, centered) */
.stage {
	--orbit-inner-size: 260px;
	--orbit-middle-size: 335px;
	--orbit-outer-size: 410px;
	--orbit-fourth-size: 485px;
	--orbit-fifth-size: 560px;
	--title-x: 50%;
	--title-y: 50%;
	--title-offset: 1rem;
	width: 85%;
	height: var(--orbit-fifth-size);
	position: relative;
	overflow: visible;
	margin: auto; /* Center the stage */
	--hue: 210; /* Hue variable for animation */
}

/* Animate hue cycling */
@keyframes hue-cycle {
	to {
		--hue: 570; /* Full cycle + 210 = 360 + 210 */
	}
}

.stage {
	animation: hue-cycle 6s linear infinite;
}

/* System container */
.system {
	position: absolute;
	inset: 0;
	display: grid;
	place-items: center;
}

/* Title with anchor positioning */
.title {
	margin: 0;
	font-size: clamp(3.45rem, 9.2vw, 8.05rem); /* Increased by 15% */
	line-height: 1;
	display: grid;
	place-items: center;
	text-align: center;
	background: linear-gradient(
		90deg,
		oklch(0.75 0.2 calc(var(--hue) * 1deg)),
		oklch(0.75 0.2 calc((var(--hue) + 60) * 1deg)),
		oklch(0.75 0.2 calc((var(--hue) + 120) * 1deg))
	);
	-webkit-background-clip: text;
	background-clip: text;
	color: transparent;
	filter: drop-shadow(0 2px 6px hsl(0 0% 0% / 0.35));
	cursor: grab;
	user-select: none;
	-webkit-user-select: none;
	touch-action: none;
	position: absolute;
	left: var(--title-x, 50%);
	top: var(--title-y, 50%);
	transform: translate(-50%, calc(-50% + 1.8rem));
}

.title:active {
	cursor: grabbing;
}

.title:focus-visible {
	outline: 2px dashed oklch(0.7 0.15 calc(var(--hue) * 1deg) / 0.8);
	outline-offset: 6px;
}

/* Three concentric orbital rings */
.orbit {
	position: absolute;
	border-radius: 50%;
	border-width: var(--orbit-thickness, 2px);
	border-style: solid;
	border-color: oklch(
		0.7 0.15 calc(var(--hue) * 1deg) /
		calc(0.3 + var(--orbit-opacity, 0))
	);
	pointer-events: none;
	/* Orbits read these values; JS controls the trailing motion */
	left: var(--orbit-x, var(--title-x, 50%));
	top: var(--orbit-y, var(--title-y, 50%));
	/* Transform with configurable rotation */
	transform-origin: center;
	transform: translate(-50%, -50%) rotateZ(var(--orbit-rotate, 0deg));
}

.orbit--inner {
	--orbit-opacity: 0.1;
	--orbit-thickness: 3px;
	--orbit-rotate: 5deg;
	width: var(--orbit-inner-size);
	height: var(--orbit-inner-size);
}

.orbit--middle {
	--orbit-opacity: 0.15;
	--orbit-thickness: 4px;
	--orbit-rotate: 10deg;
	width: var(--orbit-middle-size);
	height: var(--orbit-middle-size);
}

.orbit--outer {
	--orbit-opacity: 0.2;
	--orbit-thickness: 5px;
	--orbit-rotate: 15deg;
	width: var(--orbit-outer-size);
	height: var(--orbit-outer-size);
}

.orbit--fourth {
	--orbit-opacity: 0.25;
	--orbit-thickness: 5px;
	--orbit-rotate: 22deg;
	width: var(--orbit-fourth-size);
	height: var(--orbit-fourth-size);
}

.orbit--fifth {
	--orbit-opacity: 0.3;
	--orbit-thickness: 6px;
	--orbit-rotate: 28deg;
	width: var(--orbit-fifth-size);
	height: var(--orbit-fifth-size);
}
